/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.screen-reader-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden; }

.main-container {
  margin: 0 auto;
  max-width: 1750px;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.main-wrapper {
  max-height: 1200px;
  background-color: #eef8f6;
  margin: 80px 10px 0 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center; }

.page-header__logo-image {
  position: absolute;
  left: 50px;
  top: -40px; }

.page-header__logo-link {
  text-decoration: none;
  color: #27354f;
  width: 170px;
  height: 65px;
  letter-spacing: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 38px;
  font-family: "Rochester", cursive;
  border: 3px solid #27354f;
  transition: 0.5s;
  box-sizing: border-box; }
  .page-header__logo-link:hover {
    border: 3px solid rgba(0, 0, 0, 0);
    transition: 0.5s; }
  .page-header__logo-link::before {
    content: '';
    width: 170px;
    height: 3px;
    position: absolute;
    background: #27354f;
    bottom: 1px;
    transition: 1s;
    transform: scale(0); }
  .page-header__logo-link:hover::before {
    transform: scale(1); }

@media (max-width: 540px) {
  .page-header__logo-image {
    left: 30px;
    top: -26px; }
  .page-header__logo-link {
    width: 120px;
    height: 45px;
    font-size: 28px;
    border: 2px solid #27354f; }
    .page-header__logo-link:hover {
      border: 2px solid rgba(0, 0, 0, 0); }
    .page-header__logo-link::before {
      content: '';
      width: 120px;
      height: 2px; } }

.page-navigation__items {
  display: flex;
  padding: 145px 0 0px 0px;
  max-width: 1200px; }

.page-navigation__item {
  margin: 0 50px 0 0;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  letter-spacing: 2px; }
  .page-navigation__item:last-child {
    margin: 0; }

.page-navigation__link {
  color: rgba(39, 53, 79, 0.4);
  text-decoration: none;
  width: 100%;
  transition: 0.2s; }
  .page-navigation__link--active {
    text-decoration: none;
    color: #27354f;
    position: relative; }
    .page-navigation__link--active:before {
      content: "";
      width: calc(100% - 10px);
      height: 1px;
      bottom: -2px;
      background: #27354f;
      position: absolute; }
  .page-navigation__link:hover {
    color: #27354f; }

@media (max-width: 1300px) {
  .page-navigation {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    background: #eef8f6;
    z-index: 110; }
    .page-navigation__items {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100vw;
      height: 100vh;
      padding: 0; }
    .page-navigation__item {
      margin: 15px;
      font-size: 36px;
      font-weight: 600; }
      .page-navigation__item:last-child {
        margin: 15px; }
    .page-navigation__link:before {
      width: 0;
      height: 0; } }

@media (max-width: 540px) {
