.main-section__container {
  max-width: 1200px;
  max-height: 750px;
  display: flex;
  flex-wrap: wrap;
  overflow-y: scroll;
  margin: 85px 25px 100px 25px; }

.main-section__link {
  width: 320px;
  height: 320px;
  margin: 0 70px 70px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-size: 46px;
  text-align: center;
  line-height: 100px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  transition: 0.5s;
